import axios from 'axios'


// 这一部分是为了使用  获取token而添加的
import { getToken } from '../utils/auth'

axios.defaults.headers['Content-Type'] = 'application/x-www-form-urlencoded';        //配置请求头
// axios.defaults.headers['Cache-Control'] = 'no-cache';        //配置请求头
// axios.defaults.headers['Authorization'] = 'Basic ZGV2OjEyMzQ1Ng==';        //配置请求头
// Authorization: Basic ZGV2OjEyMzQ1Ng==
const instance=axios.create({
    // baseURL:'http://www.baidu.com', //基础URL,如果请求多个服务器的api，可以不用直接定义
    timeout:2000, //请求延时时间 
    // headers: {'Content-Type': 'application/x-www-form-urlencoded'},   //自定义请求头内容
    // headers: {'X-Requested-With': 'XMLHttpRequest'},   //自定义请求头内容
    // headers: { 'Content-Type': 'application/json' },
    // responseType: 'json',  //请求数据类型包括  'arraybuffer', 'blob', 'document', 'json', 'text', 'stream'
    // contentType: 'application/json'
    //  params: {},  //无论请求为何种类型，在params中的属性都会以key=value的格式在url中拼接
    //  proxy: {
    //    //代理
    //     host: '127.0.0.1',
    //     port: 9000,
    //     auth: {
    //       username: 'mikeymike',
    //       password: 'rapunz3l'
    //     }
    // },
    // grant_type : "authorization_code",
})

// 自定义添加请求拦截器
instance.interceptors.request.use(function (config) {
  if(config.method=='get'){
    // console.log('当前使用get方法')
    config.params=config.data
  }
    
    // 在发送请求之前做些什么

    const token = getToken('access_token')
    console.log('token：',token)

    if (token) {
      console.log('让每个请求携带自定义 token 请根据实际情况自行修改：',token)
      config.headers['Authorization'] = 'Bearer ' + token // 让每个请求携带自定义 token 请根据实际情况自行修改
    }
    return config;
  }, function (error) {
    // 对请求错误做些什么
    // Message.error('参数错误')
    return Promise.reject(error);
  });

// 自定义添加响应拦截器
instance.interceptors.response.use(function (response) {
    // 对响应数据做点什么
    return response.data;
  }, function (error) {
    // 对响应错误做点什么
    return Promise.reject(error);
  });

//将配置暴露出去
  export default instance;
