import request from '../util/axios.config'
//如果请求多个服务器的api，可以这样定义
const baseURL1='http://www.baidu.com';
const baseURL2='http://www.baidu.com';
// const equIP='http://10.129.10.243:8000';
const agvIP='http://10.129.10.251:8080';
const itIP='https://dev.iiot.hxct.com';

// 获取疫情地图参数
export function GetAgvStatus(param){
    return request({
        url:agvIP+'/api/v1/dps/mes/AgvStatus',
        method:'post' ,
        data: param, 
    })
} 

export function GetEquStatus(paramsdata){
    return request({
        url:'/demo/getdevicestatus',
        // url:'/demo/getdevicestatus?dname=syswelding3&dcommand=search',
        method:'get',
        data: paramsdata,
      
    })
} 

export function httpGet(url,param){
    console.log('我封装的axios：get方法')
    return request({
        url:url,       
        method:'get',
        data: param,
    })
} 

export function httpPost(url,param){
    console.log('我封装的axios：post方法')
    return request({
        url:url,       
        method:'post',
        data: param,
    })
}
export function httpPost1(url){
    console.log('我封装的axios：post方法')
    return request({
        url:url,       
        method:'post',
        // data: param,
    })
}

export function TaskFinished(params){
    return request({
        url:itIP+'/api/unmanned-manufacture/processing/taskFinished',
        method:'post',
        data:params
    })
} 

// 登录
export function Login(params){
    return request({
        url:baseURL1+'admin/login',
        method:'post',
        data:params
    })
}

// 修改信息
export function UpdateInfo(params){
    return request({
        url:baseURL2+'admin/user/UpdatePsw',
        method:'post',
        data:params
    }) 
}

export default{
    httpGet,
    httpPost,
    httpPost1
}