<template>
  <div>
    <div>
      <a-select
        v-if="data"
        show-search
        allowClear
        :value="value"
        placeholder="input search text"
        style="width: 200px"
        :default-active-first-option="false"
        :show-arrow="false"
        :filter-option="filterOption"
        :not-found-content="null"
        @change="handleChange"
      >
        <a-select-option v-for="d in data" :key="d.value">{{ d.text }}</a-select-option>
      </a-select>
    </div>
    <div style="background-color: #ececec; padding: 5px;">
      <a-row :gutter="16">
        <a-col :span="3">
          <span>检验时间：</span>
          <!-- <a-input placeholder="Basic usage" v-model="selectData.jy_time" /> -->
          <span v-if="selectData.jy_time">{{selectData.jy_time}}</span>
          <span v-else>待补充</span>
        </a-col>
        <a-col :span="3">
          <span>检验医院：</span>
          <span v-if="selectData.area">{{selectData.area}}</span>
          <span v-else>待补充</span>
        </a-col>
        <a-col :span="3">
          <span>报告单时间：</span>
          <span v-if="selectData.bgd_time">{{selectData.bgd_time}}</span>
          <span v-else>待补充</span>
        </a-col>
        <a-col :span="3">
          <span>申请单时间：</span>
          <span v-if="selectData.sqd_time">{{selectData.sqd_time}}</span>
          <span v-else>待补充</span>
        </a-col>
        <a-col :span="3">
          <span>检查费用：</span>
          <span>￥185.9</span>
        </a-col>
        <a-col :span="24">
          <span>备注：</span>
          <span v-if="selectData.remark">{{selectData.remark}}</span>
          <span v-else>待补充</span>
        </a-col>
      </a-row>
    </div>
    <a-tabs default-active-key="1" @change="callback">
      <a-tab-pane key="1" tab="结果报告单">
        <img
          v-image-preview
          v-if="selectData.xcg_img_src_bgd"
          :src="selectData.xcg_img_src_bgd"
          alt="暂无数据"
        />
        <span v-else>暂无数据</span>
      </a-tab-pane>
      <a-tab-pane key="2" tab="申请单" force-render>
        <img v-image-preview v-if="selectData.xcg_img_src_sqd" :src="selectData.xcg_img_src_sqd" />
        <span v-else>暂无数据</span>
      </a-tab-pane>
      <a-tab-pane key="3" tab="缴费引导单">
        <img
          v-image-preview
          v-if="selectData.xcg_img_src_jfydd"
          :src="selectData.xcg_img_src_jfydd"
          alt="暂无数据"
        />
        <span v-else>暂无数据</span>
      </a-tab-pane>
    </a-tabs>
  </div>
</template>
<script>
export default {
  components: {},
  data() {
    return {
      collapsed: false,
      data: [],
      value: undefined,
      selectData: {
        jy_time: null,
        bgd_time: null,
        sqd_time: null,
        xcg_img_src_bgd: null,
        xcg_img_src_sqd: null,
        xcg_img_src_jfydd: null,
        area: null,
        remark: null
      },
      dataList: [
        {
          jy_time: "2021-06-09",
          bgd_time: "2021-06-09",
          sqd_time: "2021-05-06",
          // xcg_img_src_bgd: require("@assets/xcg/20210121-bgd.png"),
          xcg_img_src_sqd: require("@assets/gdpyBscan/" +
            "20210506" +
            "-sqd.png"),
          xcg_img_src_jfydd: require("@assets/gdpyBscan/" +
            "20210506" +
            "-jfydd.png"),
          area: "协和",
          remark: "肝、胆、脾、胰腺未见明显异常。"
        },
        {
          jy_time: "2021-02-05",
          bgd_time: "2021-02-05",
          sqd_time: "2021-02-04",
          // xcg_img_src_bgd: require("@assets/xcg/20210121-bgd.png"),
          xcg_img_src_sqd: require("@assets/gdpyBscan/" +
            "20210204" +
            "-sqd.png"),
          xcg_img_src_jfydd: null,
          area: "协和",
          remark: "肝质地欠匀；胆、脾、胰腺未见明显异常。"
        }
      ]
    };
  },
  methods: {
    callback(key) {
      console.log(key);
    },

    filterOption(input, option) {
      return (
        option.componentOptions.children[0].text
          .toLowerCase()
          .indexOf(input.toLowerCase()) >= 0
      );
    },
    handleChange(value) {
      console.log("选定的日期是:", value);
      this.value = value;
      let index = this.dataList.findIndex(item => {
        return item.jy_time === value;
      });
      console.log("index::: ", index);
      if (index == -1) {
        return;
      }
      let pictrueName =
        value.substring(0, 4) + value.substring(5, 7) + value.substring(8, 10);
      console.log("pictrueName::: ", pictrueName);
      this.selectData = this.dataList[index];
      this.selectData.xcg_img_src_bgd = require("@assets/gdpyBscan/" +
        pictrueName +
        "-bgd.png");
    },
    unique1(array) {
      var r = [];
      for (var i = 0, l = array.length; i < l; i++) {
        for (var j = i + 1; j < l; j++) if (array[i] == array[j]) j == ++i;
        r.push(array[i]);
      }
      return r;
    },
    createComprisonFunction(propertyName) {
      return function(object1, object2) {
        var value1 = object1[propertyName];
        var value2 = object2[propertyName];
        if (value1 < value2) {
          return -1;
        } else if (value1 > value2) {
          return 1;
        } else {
          return 0;
        }
      };
    },
    getDateList() {
      const files = require
        .context("@assets/gdpyBscan", false, /bgd.png$/)
        .keys();
      console.log("*********************", files);
      const dataList = files.map(item => {
        let data = item.slice(2).substring(0, 8);
        let data1 =
          data.substring(0, 4) +
          "-" +
          data.substring(4, 6) +
          "-" +
          data.substring(6, 8);
        return data1;
      });

      let unique = this.unique1(dataList.reverse());

      unique.map(item => {
        this.data.push({
          value: item,
          text: item
        });
      });
    }
  },
  mounted() {
    this.getDateList();
  }
};
</script>
<style lang="less" scoped>
img {
  max-width: 100%;
  height: auto;
}
</style>